#about-president {
  padding-top: 140px;
}

.about-president {
  padding: 0 200px;
}

.about-president__img-wrapper {
  float: left;
  margin-right: 80px;
}

.about-president__text {
  margin-top: 40px;
}

.about-president__text:nth-child(4) {
  margin-top: 80px;
}

.about-president__text:last-child {
  color: #00488e;
}

@media (max-width: 1500px) {
  #about-president {
    padding-top: 40px;
  }

  .about-president {
    padding: 0 20px;
  }
}

@media (max-width: 1050px) {
  .about-president__title {
    font-size: 43px;
  }
}

@media (max-width: 930px) {
  .about-president__img-wrapper {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
  }
}

@media (max-width: 576px) {
  #about-president {
    padding-top: 0;
  }
}
