#equipment {
  padding-top: 140px;
}

.equipment__title {
  text-align: center;
}

.equipment__wrapper {
  margin-top: 40px;
}

.equipment__item {
  display: flex;
  margin-top: 40px;
  padding: 60px 0;
  border-bottom: 1px solid #e6edf3;
}

.equipment__item:first-child {
  border-top: 1px solid #e6edf3;
}

.equipment__item_wrapper:first-child {
  flex-basis: 20%;
  margin-right: 280px;
}

.equipment__item_title {
  font-weight: 600;
  font-size: 32px;
  line-height: 120%;
  color: #0f1e2d;
}

.equipment__item-list {
  display: flex;
  flex-direction: column;
}

.equipment__item-list a {
  font-weight: 600;
  font-size: 20px;
  line-height: 200%;
  letter-spacing: 0.02em;
  text-decoration: underline;
  color: #00488e;
}

@media (max-width: 1250px) {
  .equipment__item_wrapper:first-child {
    flex-basis: 20%;
    margin-right: 60px;
  }
}

@media (max-width: 1150px) {
  .equipment__item {
    flex-direction: column;
  }

  .equipment__item_title {
    margin-bottom: 20px;
  }
}

@media (max-width: 768px) {
  .equipment__item-list a {
    line-height: 135%;
    margin-bottom: 15px;
  }
}
