#company-factory {
  padding-top: 140px;
}

.company-factory {
  display: flex;
  width: 100%;
}

.company-factory__item:first-child {
  background-color: #00488e;
  flex-basis: 39%;
}

.company-factory__item-wrapper {
  padding: 80px;
}

.company-factory__pre-title {
  font-weight: normal;
  font-size: 20px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #e6edf3;
}

.company-factory__title {
  font-weight: 600;
  font-size: 36px;
  line-height: 130%;
  color: #fff;
  margin-top: 10px;
}

.company-factory__info-list {
  display: flex;
  flex-direction: column;
  margin-top: 60px;
}

.company-factory__list-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.company-factory__list-item:not(:last-child) {
  padding-bottom: 24px;
  margin-bottom: 24px;
  border-bottom: 1px solid #adc7e1;
}

.company-factory__list-item_text {
  font-weight: 600;
  font-size: 18px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #fff;
}

.company-factory__list-item_small-text {
  font-weight: normal;
  font-size: 16px;
}

@media (max-width: 1600px) {
  .company-factory__item-wrapper {
    padding: 80px;
  }

  .company-factory__item:first-child {
    flex-basis: 60%;
  }

  .company-factory__item:last-child {
    width: 100%;
  }

  .company-factory__item img {
    width: 100%;
    height: 655px;
  }
}

@media (max-width: 1250px) {
  .company-factory__item:first-child {
    flex-basis: 70%;
  }

  .company-factory__item-wrapper {
    padding: 80px 20px;
  }
}

@media (max-width: 991px) {
  #company-factory {
    padding-top: 40px;
  }

  .company-factory__list-item_text {
    font-size: 15px;
  }
}

@media (max-width: 768px) {
  .company-factory {
    flex-direction: column;
  }

  .company-factory__item:first-child {
    order: 1;
  }

  .company-factory__item img {
    height: auto;
  }

  .company-factory__item-wrapper {
    padding: 20px;
  }

  .company-factory__info-list {
    margin-top: 20px;
  }
}
