.footer {
  padding-top: 140px;
}

.footer-block {
  display: flex;
  justify-content: space-between;
}

.footer-bottom {
  padding: 40px 0;
  justify-content: flex-start;
}

.footer-top {
  padding-bottom: 40px;
}

.footer-logo__text {
  font-weight: normal;
  font-size: 16px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
  margin-top: 10px;
}

.footer-contact__tel-link {
  font-weight: normal;
  font-size: 16px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
  text-decoration: none;
}

.footer-contact__link {
  font-weight: normal;
  font-size: 16px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #00488e;
  text-decoration: none;
  border-bottom: 1px solid #00488e;
  padding-bottom: 1px;
}

.copyrighting {
  font-weight: normal;
  font-size: 12px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
  margin-top: 20px;
}

.footer-bottom__item:first-child {
  width: 632px;
}

.footer-bottom__item:nth-child(2) {
  max-width: 536px;
}

.reference {
  font-weight: normal;
  font-size: 12px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
}

.development-link {
  font-weight: normal;
  font-size: 12px;
  line-height: 130%;
  letter-spacing: 0.02em;
  color: #616161;
  text-decoration: none;
  display: flex;
  align-items: flex-start;
  margin-top: 70px;
}

.development-link img {
  margin-left: 5px;
}

.general-info__form-text {
  font-size: 18px;
  margin-top: 20px;
}

.footer-logo__img-link img {
  width: 160px;
}

@media (max-width: 1615px) {
  .footer-bottom {
    justify-content: space-between;
  }
}

@media (max-width: 1310px) {
  .footer-bottom__item:first-child {
    width: initial;
  }
}

@media (max-width: 1120px) {
  .footer-bottom__item:nth-child(2) {
    max-width: 320px;
  }
}

@media (max-width: 992px) {
  #footer {
    padding-top: 40px;
  }

  .footer-block {
    flex-direction: column;
  }

  .footer-top__item:last-child {
    order: 1;
  }

  .footer-top__item:nth-child(2) {
    order: 2;
  }

  .footer-contact__tel-link:first-child {
    color: #00488e;
    border-bottom: 1px solid #00488e;
  }

  .footer-bottom__item:first-child {
    order: 2;
  }

  .footer-bottom__item:last-child {
    order: 1;
  }

  .reference {
    font-weight: normal;
    font-size: 12px;
    line-height: 160%;
    letter-spacing: 0.02em;
    color: #9a9da0;
  }

  .copyrighting {
    color: #9a9da0;
  }

  .development-link {
    margin-top: 30px;
    color: #9a9da0;
  }

  hr {
    color: #9a9da0;
    width: 93%;
    margin: 0 auto;
  }

  .footer-logo__text {
    margin-bottom: 24px;
  }
}
