#serv-content {
  padding-top: 140px;
}

.serv-content {
  display: flex;
  position: relative;
}

.serv-main-content {
  padding-left: 160px;
}

.aside {
  position: sticky;
  position: -webkit-sticky;
  top: 140px;
  height: 500px;
  width: 550px;
}

.aside-menu {
  display: flex;
  flex-direction: column;
  counter-reset: item;
}

.aside-link {
  font-weight: normal;
  font-size: 18px;
  line-height: 150%;
  letter-spacing: 0.02em;
  color: #9a9da0;
  cursor: pointer;
  margin-bottom: 55px;
  padding-left: 30px;
  border-left: 1px solid #9a9da0;
  text-decoration: none;
}

.aside-link::before {
  content: '0' counter(item);
  counter-increment: item;
  display: flex;
  flex-direction: column;
  font-size: 12px;
  line-height: 15px;
  margin-bottom: 10px;
}

.aside-link.active {
  color: #00488e;
  border-left: 1px solid #00488e;
}

#general-info {
  padding-right: 250px;
}

.general-info__text {
  font-weight: normal;
  font-size: 20px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
}

.general-info__title {
  font-weight: 600;
  font-size: 28px;
  line-height: 140%;
  color: #00488e;
  margin: 50px 0 24px;
}

.general-info__list ul li {
  list-style: disc;
  margin-left: 20px;
}

#delivery-equipment {
  padding-top: 140px;
}

.serv-title {
  margin-bottom: 20px;
}

.delivery-equipment__img-wrapper {
  margin: 40px 0;
}

.delivery-equipment__text {
  width: 520px;

  a {
    color: #00488e;
    text-decoration: none;
    padding-bottom: 1px;
    border-bottom: 1px solid #00488e;
  }
}

#service-form {
  display: flex;
  padding-top: 140px;
  justify-content: space-between;
}

.service-form__item {
  width: 520px;
}

.service-form__item:first-child {
  background-color: #00488e;
}

.service-form__wrapper {
  padding: 50px 0;
}

.service-form__tab-header {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 420px;
  margin-top: 20px;
  margin-bottom: 30px;
  background: rgba(15, 30, 45, 0.25);
  border-radius: 150px;
}

.service-form__tab-link {
  font-size: 14px;
  line-height: 120%;
  text-align: center;
  color: #f0f0f0;
  opacity: 0.7;
  cursor: pointer;
  background: rgba(15, 30, 45, 0);
  padding: 13px 25px;
  border-radius: 150px;
  width: 220px;
}

.service-form__tab-link.active {
  color: #0f1e2d;
  background: #fff;
  opacity: 1;
}

.serv-form-content {
  width: 420px;
}

.serv-form-input {
  width: 420px;
}

.serv-form-textarea {
  width: 420px;
}

@media (max-width: 1450px) {
  .serv-main-content {
    padding-left: 20px;
  }
}

@media (max-width: 1300px) {
  .serv-main-content {
    width: 905px;
  }
}

@media (max-width: 1200px) {
  #serv-content {
    padding-top: 40px;
  }

  .serv-content {
    flex-direction: column;
  }

  #general-info {
    padding-right: 0;
  }

  .aside {
    position: static;
    top: 0;
    width: 100%;
    height: auto;
  }

  .aside-menu {
    flex-direction: row;
    justify-content: space-between;
  }

  .aside-link {
    margin-bottom: 0;
    padding-left: 0;
    border: none;
  }

  .aside-link.active {
    border: none;
  }

  .serv-main-content {
    width: 100%;
    padding-left: 0;
    margin-top: 20px;
  }

  #delivery-equipment {
    padding-top: 20px;
  }

  #service-form {
    padding-top: 20px;
  }
}

@media (max-width: 1150px) {
  #service-form {
    flex-direction: column;
  }

  .service-form__item:last-child {
    order: -1;
    margin-bottom: 20px;
  }
}

@media (max-width: 560px) {
  .serv-form-content {
    width: 260px;
  }

  .serv-form-content input {
    width: 260px;
  }

  .serv-form-content textarea {
    width: 260px;
  }

  .service-form__tab-header {
    width: 350px;
  }

  .service-form__tab-link {
    font-size: 11px;
  }

  .general-info__title {
    margin: 20px 0;
    font-size: 20px;
  }

  .aside-link {
    font-size: 14px;
  }

  .general-info__text {
    font-size: 14px;
  }

  .delivery-equipment__text {
    width: 100%;
  }

  .service-form__item {
    width: 100%;
  }
}

@media (max-width: 415px) {
  .service-form__tab-header {
    flex-direction: column;
    border-radius: 20px;
    width: 279px;
  }

  .service-form__tab-link.active {
    width: 100%;
  }
}
