#form-block {
  padding-top: 140px;
}

.form-block {
  display: flex;
}

.form-block__item {
  flex-basis: 50%;
  padding: 90px 0 70px;
}

.form-block__item:first-child {
  background-color: #00488e;
}

.form-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form-title {
  font-weight: 600;
  font-size: 28px;
  line-height: 140%;
  text-align: center;
  color: #fff;
}

.form-content {
  display: flex;
  flex-direction: column;
  width: 320px;
  margin-top: 30px;
}

.form-input {
  padding: 16px 20px;
  border: none;
  border-radius: 2px;
  margin-bottom: 10px;
  width: 320px;
  z-index: 1;
}

.form-textarea {
  padding: 16px 20px;
  border: none;
  border-radius: 2px;
  width: 320px;
  z-index: 1;
}

.form-input::placeholder {
  font-weight: normal;
  font-size: 15px;
  line-height: 19px;
  letter-spacing: 0.02em;
  color: #0f1e2d;
}

.form-input::after {
  content: '*';
  position: relative;
  font-size: 16px;
  color: red;
  margin-left: 1px;
  display: block;
}

.form-textarea::placeholder {
  font-weight: normal;
  font-size: 15px;
  line-height: 19px;
  letter-spacing: 0.02em;
  color: #0f1e2d;
}

.form-btn {
  padding: 16px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #fff;
  border-radius: 2px;
  color: #fff;
  background: transparent !important;
}

.form-block__item:last-child {
  display: flex;
  justify-content: center;
}

.form-block__application-text {
  max-width: 434px;
  font-weight: normal;
  font-size: 20px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
  margin-top: 48px;
}

label {
  position: relative;
  max-width: 434px;
}

label span {
  position: absolute;
  top: 15px;
  left: 18px;
  font-size: 14px;
  cursor: text;
}

textarea:optional + span::before {
  content: attr(placeholder);
  position: relative;
  color: #0f1e2d;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.02em;
}

input:optional + span::before {
  content: attr(placeholder);
  position: relative;
  color: #0f1e2d;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.02em;
}

input:invalid + span::before {
  content: attr(placeholder);
  position: relative;
  color: #0f1e2d;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.02em;
}

input:invalid + span::after {
  content: '*';
  position: relative;
  font-size: 16px;
  color: red;
  margin-left: 1px;
}

textarea:invalid + span::before {
  content: attr(placeholder);
  position: relative;
  color: #0f1e2d;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.02em;
}

input:focus + span::before {
  content: '';
}

// input:valid + span::before {
//   content: '';
// }

// textarea:valid + span::before {
//   content: '';
// }

textarea:focus + span::before {
  content: '';
}

textarea:invalid + span::after {
  content: '*';
  position: relative;
  font-size: 16px;
  color: red;
  margin-left: 1px;
}

@media (max-width: 991px) {
  #form-block {
    padding-top: 40px;
  }

  .form-block {
    flex-direction: column;
  }

  .form-block__item:first-child {
    order: 1;
  }

  .form-block__item:last-child {
    padding: 0 20px;
    margin-bottom: 20px;
  }

  .form-block__application-text {
    max-width: 270px;
  }
}
