#history {
  padding-top: 140px;
}

.history {
  display: flex;
  flex-direction: column;
}

.history-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 140px;
}

.history-info {
  flex-basis: 60%;
}

.history-year {
  font-weight: 600;
  font-size: 78px;
  line-height: 110%;
  color: #00488e;
}

.history-title {
  font-style: normal;
  font-weight: 600;
  font-size: 36px;
  line-height: 130%;
  color: #0f1e2d;
}

.history-text {
  margin-top: 40px;
}

.blue-text {
  color: #00488e;
}

.history-img__wrapper {
  margin: 40px 0;
}

.title-indent {
  margin-top: 60px;
}

.history-img__block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.history-img__block img {
  padding-bottom: 10px;
}

.history-president {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
}

.history-president__item:first-child {
  flex-basis: 55%;

  .blue-text {
    margin-top: 0;
  }
}

.history-reward {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
}

.history-reward__item:last-child {
  flex-basis: 76%;

  .history-text:first-child {
    margin-top: 0;
  }
}

.history-sertificate {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
}

.history-sertificate__item:last-child {
  flex-basis: 60%;
}

.history-youtube {
  margin-top: 40px;
}

.history-youtube iframe {
  width: 100%;
}

.history-text span {
  font-weight: 600;
}

.history-conclusion {
  background: #00488e;
  color: #fff;
  padding: 60px;
  font-weight: 600;
  font-size: 28px;
  line-height: 140%;
}

.text-indent {
  margin-top: 0;
}

@media (max-width: 1570px) {
  .history-info {
    flex-basis: 70%;
  }
}

@media (max-width: 991px) {
  .history-sertificate {
    flex-direction: column;
  }

  .history-sertificate__item {
    padding-bottom: 10px;
  }

  .history-reward {
    flex-direction: column;
  }

  .history-reward__item {
    padding-bottom: 10px;
  }

  .history-president {
    flex-direction: column;
  }

  .history-president__item:last-child {
    order: -1;
    padding-bottom: 10px;
  }
}

@media (max-width: 576px) {
  .history-item {
    margin-bottom: 40px;
  }

  .history-year {
    font-size: 55px;
    margin-bottom: 20px;
  }

  .history-conclusion {
    padding: 10px;
    font-size: 19px;
  }

  .title-indent {
    margin-top: 20px;
  }

  .history-text {
    margin-top: 20px;
  }
}

@media (max-width: 450px) {
  .history-item {
    flex-direction: column;
  }
}
