#clients {
  padding-top: 140px;
}

.clients {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-left: 180px;
  padding-right: 180px;
}

.clients-item {
  width: 335px;
  margin-bottom: 80px;
}

.clients-item__title {
  font-weight: 600;
  font-size: 24px;
  line-height: 140%;
  color: #00488e;
  max-width: 335px;
  padding-bottom: 20px;
  border-bottom: 1px solid #adc7e1;
  margin-bottom: 21px;
}

.clients-list__item {
  font-weight: normal;
  font-size: 16px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
  margin-bottom: 10px;
}

.clients-list-more {
  color: #00488e;
  font-weight: normal;
  font-size: 16px;
  line-height: 200%;
  letter-spacing: 0.02em;
  cursor: pointer;
  text-decoration: underline;
}

@media (max-width: 1200px) {
  #clients {
    padding-top: 20px;
  }

  .clients {
    padding: 0 20px;
  }
}

@media (max-width: 768px) {
  .clients-item {
    margin-bottom: 20px;
  }
}
