#question-form {
  padding-top: 140px;
}

.question-form {
  background: #e6edf3;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 80px 0;
}

.question-form__item:first-child {
  margin-right: 154px;
}

.question-form__title {
  max-width: 450px;
}

.question-form__content {
  width: 420px;

  input {
    width: 420px;
  }

  textarea {
    width: 420px;
  }
}

.question-form__aftertitle {
  font-weight: normal;
  font-size: 18px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
  margin-top: 20px;
}

.question-form__wrapper {
  background: #00488e;
  border-radius: 2px;
  padding: 50px;
}

.question-form__form-title {
  font-weight: 600;
  font-size: 28px;
  line-height: 140%;
  text-align: center;
  color: #fff;
}

@media (max-width: 1250px) {
  #question-form {
    padding-top: 20px;
  }

  .question-form__item:first-child {
    margin-right: 0;
  }
}

@media (max-width: 1070px) {
  .question-form {
    flex-direction: column;
    padding: 20px;
  }

  .question-form__item:last-child {
    margin-top: 20px;
  }
}

@media (max-width: 600px) {
  .question-form__content {
    width: 320px;
  }

  .question-form__content input {
    width: 320px;
  }

  .question-form__content textarea {
    width: 320px;
  }
}

@media (max-width: 490px) {
  .question-form__content {
    width: 250px;
  }

  .question-form__wrapper {
    padding: 30px;
  }

  .question-form__content input {
    width: 250px;
  }

  .question-form__content textarea {
    width: 250px;
  }

  .question-form__content button {
    padding: 16px 60px;
  }
}

@media (max-width: 400px) {
  .question-form__content {
    width: 180px;
  }

  .question-form__wrapper {
    padding: 30px;
  }

  .question-form__content input {
    width: 180px;
  }

  .question-form__content textarea {
    width: 180px;
  }

  .question-form__content button {
    padding: 16px 30px;
  }
}
