.sitemap {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  background: #fff;
  width: 500px;
  z-index: 600;
  transition: 0.5s;
  transform: translateX(-500px);
}

.sitemap.open {
  transform: translateX(0);
  box-shadow: 0 0 30px 3px rgba(0, 0, 0, 0.5);
}

.sitemap__wrapper {
  display: flex;
  flex-direction: column;
  padding: 40px;
  max-height: 100vh;
  overflow: auto;
  scrollbar-width: thin;
  scrollbar-color: #000 #eee;
}

.button-click__block {
  position: fixed;
  z-index: 104;
  right: 0;
  top: 50%;
}

.sitemap__opener {
  width: 50px;
  height: 50px;
  position: absolute;
  left: 100%;
  top: 30%;
  border-radius: 0 30px 30px 0;
  padding: 10px;
  background: #fff;
  cursor: pointer;
  box-sizing: border-box;
  border-top: 1px solid black;
  border-right: 1px solid black;
  border-bottom: 1px solid black;
  opacity: 0.3s;
  transition: 0.5s;
  display: flex;
  align-items: center;
}

.sitemap__opener_wrapper {
  display: flex;
  flex-direction: column;
  width: 30px;
  cursor: pointer;
}

.sitemap__opener div span {
  background: #000;
  border-radius: 10px;
  height: 2px;
  margin: 2px 0;
  transition: 0.4s cubic-bezier(0.68, -0.6, 0.32, 1.6);
}

.sitemap__opener span:nth-of-type(1) {
  width: 50%;
}

.sitemap__opener span:last-child {
  width: 50%;
}

.sitemap__opener span:nth-child(2) {
  width: 100%;
}

.sitemap__opener.active span:nth-of-type(1) {
  transform-origin: bottom;
  transform: rotate(45deg) translate(1px, -1px);
}

.sitemap__opener.active span:nth-of-type(2) {
  transform-origin: top;
  transform: rotate(-45deg);
}

.sitemap__opener.active span:nth-of-type(3) {
  transform-origin: bottom;
  width: 50%;
  transform: translate(12px, -1px) rotate(45deg);
}

.sitemap__wrapper nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  padding-top: 40px;
}

.sitemap__wrapper nav a {
  font-size: 22px;
  margin: 15px 0;
  line-height: 150%;
  text-decoration: none;
  color: #0f1e2d;
}

.sitemap__wrapper nav a:hover {
  color: #00488e;
}

@media (max-width: 576px) {
  .sitemap {
    width: 290px;
    transform: translateX(-290px);
  }

  .sitemap__wrapper nav {
    height: auto;
  }
}
