#company-description {
  padding-top: 140px;
}

.company-description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.about-nikko-company-work {
  max-width: 720px;
}

.company-description__wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 140px;
}

@media (max-width: 992px) {
  #company-description {
    padding-top: 20px;
  }

  .company-description__wrapper {
    margin-top: 50px;
  }

  .company-description__item-text {
    font-size: 16px;
  }
}

@media (max-width: 768px) {
  .company-description__wrapper {
    flex-direction: column;
  }

  .company-description__item:first-child {
    order: 1;
    margin-top: 30px;
  }
}
