#accordion {
  padding-top: 100px;
}

.accordion {
  max-width: 1120px;
  margin: 0 auto;
}

.accordion-title {
  padding: 40px 0;
  position: relative;
  width: 100%;
  background-color: transparent;
  text-align: left;
  font-weight: 600;
  font-size: 24px;
  line-height: 140%;
  color: #0f1e2d;
  border: none;
  border-bottom: 1px solid #0f1e2d;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.accordion-title span {
  max-width: 920px;
  display: block;
}

.accordion-title__arrow {
  width: 16px;
  height: 16px;
  border: 2px solid;
  border-color: #fff #0f1e2d #0f1e2d #fff;
  transform: rotate(45deg);
  transition: border-color 0.5s ease-in-out, bottom 0.5s ease-in-out;
  transition: 0.2s;
}

.accordion-title__arrow.active {
  border-color: #0f1e2d #fff #fff #0f1e2d;
  transition: 0.2s;
}

.accordion-description {
  font-weight: normal;
  font-size: 20px;
  line-height: 170%;
  color: #0f1e2d;
  padding: 0;
  border: none;
  height: 0;
  overflow: hidden;
  transition: 0.35s;
}

.accordion-title.active {
  color: #00488e;
  border: none;
}

.accordion-description.active {
  height: auto;
  border-bottom: 1px solid #0f1e2d;
  padding: 20px 0 30px;
  transition: 0.35s;
}

@media (max-width: 1050px) {
  #accordion {
    padding-top: 20px;
  }

  .accordion-title span {
    width: 750px;
  }
}

@media (max-width: 840px) {
  .accordion-title span {
    width: 550px;
  }
}

@media (max-width: 576px) {
  .accordion-title {
    font-size: 16px;
    padding: 20px 0;
  }

  .accordion-title span {
    width: 350px;
  }
}

@media (max-width: 411px) {
  .accordion-title span {
    width: 250px;
  }
}
