#partners {
  padding-top: 140px;
}

.partners-title {
  text-align: center;
  color: #0f1e2d;
}

.partners-slider {
  margin-top: 60px;
  position: relative;
}

.partners-slide {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.slider-line {
  border: 1px solid #e6edf3;
  width: 1px;
  height: 100px;
  position: absolute;
  display: flex;
  background-color: #e6edf3;
  right: 0;
  top: 15px;
}

.swiper-slide.swiper-slide-duplicate-prev > .slider-line {
  display: none;
}

.gallery__arrow {
  content: '';
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  border: 1px solid #00488e;
  border-radius: 50%;
  cursor: pointer;
  position: absolute;
  top: 26%;
  z-index: 103;
}

.gallery__arrow--prev {
  background: url('../images/sliderArrowLeft.svg') no-repeat center 19px;
  left: 0;
}

.gallery__arrow--next {
  background: url('../images/sliderArrowRight.svg') no-repeat center 19px;
  right: 0;
}

.partners-slide img:hover {
  transform: scale(1.3);
  transition: 0.3s;
}

@media (max-width: 991px) {
  #partners {
    padding-top: 40px;
  }
}

@media (max-width: 576px) {
  .slider-line {
    display: none;
  }
}
