#main-first-screen {
  width: 100%;
  height: 100vh;
  position: relative;
}

.video-container {
  width: 100%;
  height: 100vh;
}

.video-container video {
  width: 100%;
  height: 100vh;
  -o-object-fit: cover;
  object-fit: cover;
}

.video-container img {
  width: 100%;
  height: 100vh;
  -o-object-fit: cover;
  object-fit: cover;
}

.shadow {
  height: 100vh;
  width: 100%;
  background: linear-gradient(105.81deg, rgba(0, 72, 142, 0.64) 3.22%, rgba(0, 72, 142, 0.688) 49.42%, rgba(0, 72, 142, 0.64) 100%), url(.png);
  position: absolute;
  z-index: 103;
}

.first-screen-content {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  z-index: 104;
  top: 41%;
  width: 100%;
}

.main-title {
  text-align: center;
  color: #fff;
}

.main-subtitle {
  font-weight: normal;
  font-size: 22px;
  line-height: 160%;
  text-align: center;
  letter-spacing: 0.02em;
  color: #fff;
  margin-top: 20px;
  max-width: 735px;
}

.btn {
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.02em;
  padding: 16px 84px;
  border-radius: 2px;
  background: #fff;
  border: 1px solid #fff;
  margin-top: 40px;
}

.nikko-loc {
  position: absolute;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 104;
  color: #adc7e1;
  bottom: 60px;
}

.d-none {
  display: none;
}

@media (max-width: 768px) {
  .first-screen-content {
    padding: 0 20px;
  }
}

@media (max-width: 576px) {
  .title {
    font-size: 36px;
    line-height: 125%;
  }

  .main-subtitle {
    font-size: 18px;
  }
}

@media (max-width: 415px) {
  .nikko-loc {
    font-size: 12px;
  }
}

@media (max-width: 370px) {
  .title {
    font-size: 30px;
    line-height: 125%;
  }

  .main-subtitle {
    font-size: 16px;
  }

  .nikko-loc {
    font-size: 11px;
  }
}

@media (max-height: 415px) {
  .main-title {
    font-size: 26px;
  }

  .main-subtitle {
    font-size: 13px;
  }

  .contact-btn {
    padding: 10px 35px;
    font-size: 14px;
    margin-top: 15px;
  }

  .nikko-loc {
    bottom: 25px;
  }
}
