#modification-tabs {
  padding-top: 100px;
}

.modification-tabs__header {
  display: flex;
  justify-content: space-between;
}

.modification-tabs__link {
  width: 507px;
  padding: 8px 0;
  text-align: center;
  font-weight: normal;
  font-size: 20px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
  border-bottom: 1px solid #00488e;
  cursor: pointer;
}

.modification-tabs__link.active {
  color: #00488e;
  background: #f2f3f5;
  border-top: 2px solid #00488e;
  border-bottom: none;
}

.modification-tab {
  background: #f2f3f5;
  padding-bottom: 140px;
}

.modification-tab > .container {
  display: flex;
  padding: 80px 0;
  border-bottom: 1px solid #0f1e2d;
}

.modification-tab > .border-none {
  border: none;
}

.modification-tab__title {
  width: 320px;
  font-weight: 600;
  font-size: 32px;
  line-height: 120%;
  color: #00488e;
  margin-right: 180px;
}

.modification-tab__text {
  width: 720px;
  font-weight: normal;
  font-size: 16px;
  line-height: 160%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
}

.modification-tab__item {
  counter-reset: item;
}

.modification-tab__list-item::before {
  content: counter(item) ".";
  counter-increment: item;
}

.modification-tab__slider {
  overflow: hidden;
  position: relative;
}

.swiper-wrapper {
  padding-bottom: 50px;
}

.modification-tab__slide-title {
  font-weight: 600;
  font-size: 16px;
  line-height: 160%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
  margin: 24px 0 8px;
}

.modification-tab__slide-text {
  font-weight: normal;
  font-size: 16px;
  line-height: 160%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
  width: 520px;
}

.mod__arrow {
  content: '';
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  border: 1px solid #00488e;
  border-radius: 50%;
  cursor: pointer;
  position: absolute;
  top: 37%;
  z-index: 103;
}

.mod__arrow--prev {
  background: url(../images/sliderArrowLeft.svg) no-repeat center 19px;
  left: 20%;
}

.mod__arrow--next {
  background: url(../images/sliderArrowRight.svg) no-repeat center 19px;
  right: 30%;
}

.modification-tab__sertificates {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.modification-tab__sertificates-item {
  margin-bottom: 20px;
}

.modification-tab__sertificates_wrapper {
  width: 100%;
}

.workshop-slider__title {
  text-align: center;
}

.workshop-tabs__link {
  width: 760px;
}

@media (max-width: 1800px) {
  .mod__arrow--next {
    right: 28%;
  }
}

@media (max-width: 1750px) {
  .mod__arrow--prev {
    left: 22%;
  }

  .mod__arrow--next {
    right: 23%;
  }
}

@media (max-width: 1650px) {
  .modification-tab > .container {
    width: 93%;
  }

  .mod__arrow--next {
    right: 23%;
  }
}

@media (max-width: 1410px) {
  .modification-tabs__link {
    width: initial;
    padding: 0 40px;
  }

  .modification-tab__title {
    width: 100%;
    margin-right: 210px;
  }

  .modification-tab > .container {
    display: flex;
    justify-content: space-between;
    padding: 60px 20px;
  }

  .workshop-tabs__link {
    padding: 0 100px;
  }
}

@media (max-width: 1000px) {
  .modification-tab {
    padding-bottom: 40px;
  }

  .mod__arrow--prev {
    left: 10%;
  }

  .mod__arrow--next {
    right: 10%;
  }

  .modification-tabs__link {
    padding: 0 10px;
  }

  .modification-tab > .container {
    flex-direction: column;
  }

  .modification-tab__title {
    margin-bottom: 20px;
  }

  .workshop-tabs__link {
    padding: 0 80px;
  }
}

@media (max-width: 850px) {
  .modification-tabs__link {
    font-size: 14px;
  }

  .modification-tab__text {
    width: 100%;
  }

  .modification-tab__title {
    margin-right: 0;
  }
}

@media (max-width: 576px) {
  .mod__arrow {
    display: none;
  }

  .modification-tabs__header {
    flex-direction: column;
  }

  .modification-tab__slide-text {
    width: 100%;
  }

  .serv-form-input + span::before {
    font-size: 12px;
  }

  .serv-form-input:invalid + span::before {
    font-size: 12px;
  }

  .serv-form-textarea:invalid + span::before {
    font-size: 12px;
  }

  .modification-tabs__link {
    padding: 15px 80px;
  }
}
