#equipment-header {
  padding-top: 140px;
}

.equipment-header {
  display: flex;
}

.equipment-header__gallery {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  margin-top: 24px;
}

.equipment-header__gallery-content {
  position: relative;
  height: 443px;
}

.play-button__wrap {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.equipment-header__item {
  margin-right: 80px;
}

.equipment-header__item:first-child {
  width: 720px;
}

.equipment-header__title {
  font-style: normal;
  font-weight: 600;
  font-size: 36px;
  line-height: 130%;
  color: #00488e;
  width: 477px;
  margin-bottom: 24px;
}

.equipment-header__adv-item {
  font-weight: normal;
  font-size: 16px;
  line-height: 160%;
  color: #0f1e2d;
  margin-bottom: 16px;
}

.equipment-header__adv-item_title {
  font-style: normal;
  font-weight: 500;
  font-size: 22px;
  line-height: 160%;
  letter-spacing: 0.02em;
  color: #00488e;
}

.equipment-header__btn-block {
  display: flex;
  margin-top: 40px;
}

.equipment-header__btn {
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  display: flex;
  align-items: center;
  justify-content: center;
  letter-spacing: 0.02em;
  border: none;
  background-color: #00488e;
  border-radius: 2px;
  color: #fff;
  width: 260px;
  padding: 16px;
  margin-right: 24px;
  text-decoration: none;
}

.equipment-header__btn_transparent {
  background-color: #fff;
  color: #00488e;
  text-decoration: underline;
}

.popup {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(20, 22, 24, 0.8);
  z-index: 99999;
  display: none;
  align-items: center;
  justify-content: center;
}

.equipment-popup__tech {
  background-color: #fff;
  width: 880px;
  padding: 10px 80px;
  height: 740px;
}

.workshop-popup__tech {
  width: 1400px;
  height: 715px;
}

.close-popup {
  height: 48px;
  width: 48px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f0f0f0;
  margin-left: auto;
  cursor: pointer;
}

.tech-popup__title {
  font-weight: 600;
  font-size: 36px;
  line-height: 130%;
  color: #0f1e2d;
  margin-bottom: 20px;
}

.tech-popup__list {
  display: flex;
  justify-content: space-between;
}

// .tech-popup__list-item {
//   display: flex;
//   justify-content: space-between;
// }

.tech-popup__content-item {
  width: 320px;
  padding-bottom: 20px;
  border-bottom: 1px solid #e6edf3;
  margin-top: 20px;
}

.tech-popup__content-title {
  font-weight: 600;
  font-size: 16px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
}

.tech-popup__content-text {
  font-weight: normal;
  font-size: 16px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
}

.border-none {
  border: none;
}

.tech-popup__wrapper {
  display: flex;
  margin-bottom: 24px;
}

.tech-popup__item:last-child {
  margin-left: 40px;
}

.tech-popup__workshop-list {
  margin-top: 10px;
}

.tech-popup__workshop-list_item {
  margin-top: 15px;
}

.tech-popup__workshop-title {
  font-weight: 600;
  font-size: 28px;
  line-height: 130%;
  color: #00488e;
}

.tech-popup__workshop-text {
  font-weight: normal;
  font-size: 16px;
  line-height: 160%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
}

.tech-popup__workshop-note {
  font-weight: normal;
  font-size: 16px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
}

.tech-popup__img-wrapper img {
  width: 70%;
}

.tech-popup__img img {
  width: 35%;
}

.eq_video {
  display: none;
  width: 100%;
  height: 100%;
}

.equipment-header__gallery-link img {
  width: 161px;
  height: 121px;
}

@media (max-width: 1700px) {
  .tech-popup__img-wrapper img {
    width: 70%;
  }

  .tech-popup__img img {
    width: 35%;
  }

  .tech-popup__workshop-title {
    font-size: 28px;
  }

  .tech-popup__workshop-list {
    margin-top: 10px;
  }

  .tech-popup__workshop-list_item {
    margin-top: 14px;
  }
}

@media (max-width: 1350px) {
  .equipment-header__item:last-child {
    margin-right: 0;
  }
}

@media (max-width: 1300px) {
  .tech-popup__wrapper {
    flex-direction: column;
  }

  .tech-popup__item:last-child {
    display: flex;
    margin-top: 20px;
    margin-left: 0;
  }

  .tech-popup__workshop-list {
    display: flex;
    margin-top: 0;
    flex-wrap: wrap;
  }

  .tech-popup__img-wrapper img {
    width: 50%;
  }

  .tech-popup__workshop-title {
    font-size: 18px;
  }

  .tech-popup__workshop-text {
    font-size: 14px;
  }

  .tech-popup__workshop-list_item {
    margin-right: 20px;
  }

  .equipment-popup__tech {
    height: auto;
  }

  .tech-popup__img img {
    width: 100%;
  }

  .tech-popup__img {
    margin-right: 20px;
  }
}

@media (max-width: 1250px) {
  .equipment-header {
    flex-direction: column;
  }

  .equipment-header__item:last-child {
    margin-top: 20px;
  }
}

@media (max-width: 850px) {
  .equipment-header__gallery-content {
    height: auto;
  }

  .eq_video {
    height: 400px;
  }

  .equipment-header__item:first-child {
    width: 100%;
  }

  .equipment-header__title {
    width: 100%;
  }

  .tech-popup__img-wrapper img {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .equipment-popup__tech {
    width: 100%;
    padding: 30px;
    padding-top: 40px;
    height: 100vh;
    overflow-y: auto;
  }

  .tech-popup__workshop-note {
    font-size: 14px;
    line-height: 130%;
  }
}

@media (max-width: 576px) {
  .tech-popup__content-item {
    padding-bottom: 5px;
    margin-top: 5px;
  }

  .tech-popup__title {
    font-size: 22px;
  }

  .tech-popup__content-title {
    font-size: 12px;
  }

  .tech-popup__content-text {
    font-size: 12px;
  }

  .tech-popup__item {
    flex-direction: column;
  }

  .tech-popup__img img {
    width: 17%;
  }

  .eq_video {
    height: 300px;
  }

  .equipment-header__gallery-link img {
    width: 161px;
    height: 73px;
  }
}

@media (max-width: 420px) {
  .equipment-header__title {
    font-size: 26px;
    margin-bottom: 10px;
  }

  .equipment-header__adv-item_title {
    line-height: 120%;
  }

  .equipment-header__btn-block {
    flex-direction: column;
  }

  .equipment-header__btn {
    width: 100%;
  }

  .eq_video {
    height: 230px;
  }
}

@media (max-height: 415px) {
  .equipment-popup__tech {
    width: 100%;
    padding: 30px;
    padding-top: 40px;
    height: 100vh;
    overflow-y: auto;
  }

  .tech-popup__workshop-note {
    font-size: 14px;
    line-height: 130%;
  }
}

@media (max-width: 360px) {
  .equipment-header__gallery-link img {
    width: 161px;
    height: 61px;
  }
}
