html {
  font-size: 62.5%;
  height: max-content;
}

body {
  display: flex; // Footer at bottom
  flex-direction: column; // Footer at bottom
  color: #000;
  font-size: 1.4rem;
  line-height: 1.6rem;
  background: #fff;
  font-family: 'Gilroy', sans-serif;
  font-weight: normal;
  font-style: normal;
  height: max-content;
}

.header {
  flex-shrink: 0; // Footer at bottom
}

.main {
  position: relative;
  flex: 1 0 auto; // Footer at bottom
}

.footer {
  z-index: 10;
  flex-shrink: 0; // Footer at bottom
  margin-top: auto;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
  border: none;

  table & {
    max-width: none;
  }
}

.container {
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 40px;
}

.title {
  font-weight: 600;
  font-size: 48px;
  line-height: 140%;
}

.nikko-text {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
}

.swiper {
  overflow: hidden !important;
}

@media (max-width: 768px) {
  .container {
    padding: 0 20px;
  }
}

@media (max-width: 576px) {
  .nikko-text {
    font-size: 16px;
    line-height: 180%;
  }
}
