#reward-slider-block {
  padding-top: 140px;
}

.reward-title {
  text-align: center;
}

.reward-slider {
  margin-top: 40px;
}

.reward-slider__text {
  margin-top: 30px;
  font-weight: normal;
  font-size: 16px;
  line-height: 180%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
}

@media (max-width: 991px) {
  #reward-slider-block {
    padding-top: 40px;
  }
}
