#nikko-company-video {
  padding-top: 140px;
}

.nikko-company-video {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nikko-company__video-wrapper {
  margin-top: 40px;
  position: relative;
}

.nikko-company__video {
  display: none;
}

.nikko-com-video {
  width: 100%;
}

.icon-play__wrapper {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

@media (max-width: 1120px) {
  .nikko-company__video {
    width: 100%;
  }

  .nikko-company__video-wrapper {
    width: 100%;
  }
}

@media (max-width: 991px) {
  #nikko-company-video {
    padding-top: 40px;
  }

  .nikko-company__video {
    height: 480px;
  }
}

@media (max-width: 768px) {
  .nikko-company__video {
    height: 420px;
  }
}

@media (max-width: 576px) {
  .nikko-company__video {
    height: 320px;
  }
}

@media (max-width: 430px) {
  .nikko-company__video {
    height: 220px;
  }
}

@media (max-width: 350px) {
  .nikko-company__video {
    height: 180px;
  }
}
