#nikko-company {
  padding: 140px 0;

  .container {
    max-width: 720px;
  }
}

.nikko-company {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.nikko-company__title {
  color: #0f1e2d;
  text-align: left;
  max-width: 720px;
}

.nikko-company__text {
  font-weight: normal;
  font-size: 20px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
  margin-top: 40px;
}

.nikko-company__work {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 80px;
}

.nikko-company__numbers {
  font-weight: normal;
  font-size: 48px;
  line-height: 140%;
  color: #00488e;
}

.nikko-company__subtext {
  font-weight: normal;
  font-size: 18px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
  margin-top: 10px;
}

.nikko-company__btn {
  font-weight: normal;
  font-size: 18px;
  line-height: 150%;
  letter-spacing: 0.02em;
  color: #00488e;
  margin-top: 70px;
  display: flex;
  align-items: center;
  cursor: pointer;
  text-decoration: none;
}

.nikko-company__btn::after {
  content: '';
  display: flex;
  width: 48px;
  height: 48px;
  border: 1px solid #e6edf3;
  border-radius: 50%;
  background: url('../images/company-arrow.svg') no-repeat center 19px;
  margin-left: 24px;
}

.company-description__item-text {
  max-width: 520px;
  margin-top: 0;
  margin-right: 80px;
}

@media (max-width: 992px) {
  #nikko-company {
    padding: 40px 0;
  }
}

@media (max-width: 768px) {
  .nikko-company__work {
    justify-content: space-around;
  }
}

@media (max-width: 576px) {
  .nikko-company__text {
    font-size: 16px;
    margin-top: 15px;
  }

  .nikko-company__work {
    margin-top: 30px;
    justify-content: space-between;
  }

  .nikko-company__numbers {
    font-size: 36px;
  }

  .nikko-company__subtext {
    font-size: 16px;
    line-height: 100%;
  }

  .nikko-company__btn {
    margin-top: 40px;
  }
}
