#header {
  position: fixed;
  background-color: #fff;
  width: 100%;
  z-index: 99999;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar {
  display: flex;
  align-items: center;
}

.nav-link {
  font-weight: 600;
  font-size: 11px;
  line-height: 13px;
  text-transform: uppercase;
  color: #0f1e2d;
  text-decoration: none;
}

.nav-link:hover {
  color: #00488e;
}

.navbar-menu__link {
  font-weight: 600;
  font-size: 11px;
  line-height: 12px;
  text-transform: uppercase;
  color: #0f1e2d;
  text-decoration: none;
  cursor: pointer;
  display: flex;
  margin-right: 54px;
  padding: 30px 0 34px;
  height: 70px;
  align-items: center;
}

.navbar-menu__link:hover {
  color: #00488e;
}

.navbar-menu__link svg {
  margin-right: 16px;
}

.navbar-menu__link svg:hover {
  fill: #00488e;
}

// .burger-img {
//   margin-right: 16px;
// }

.menu-arrow__img {
  margin-left: 8px;
}

.header-search {
  display: flex;
  margin-right: 0;
}

.header-line {
  width: 1px;
  height: 30px;
  background: #e6edf3;
  margin-right: 54px;
}

.input-search {
  background-color: #e6edf3;
  border: none;
  border-radius: 2px;
  padding: 8px 10px;
}

.input-search::placeholder {
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  color: #9a9da0;
}

.search-btn {
  border: none;
  padding: 8px 14px;
  margin-left: -10px;
  border-radius: 2px;
  background: url('../images/search.svg') no-repeat center center;
  background-color: #e6edf3;
}

.lang-btn {
  text-decoration: none;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  text-align: right;
  color: #9a9da0;
  padding: 10px;
}

.lang-btn.active {
  color: #0f1e2d;
}

.contacts-btn {
  text-decoration: none;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  text-align: right;
  text-transform: uppercase;
  color: #00488e;
  border-bottom: 1px solid #00488e;
  padding-bottom: 1px;
}

#header-menu {
  display: none;
  position: relative;
}

.header-menu {
  position: absolute;
  background-color: #00488e;
  width: 100%;
  display: none;
}

#header-menu.active {
  display: block;
}

.header-main-menu.active {
  display: block;
  z-index: 9999;
}

.header-company-menu.active {
  display: block;
  z-index: 9999;
}

.first-level__navbar {
  counter-reset: item;
}

.nav-link__wrapper {
  padding: 30px 70px;
  width: 100%;
  display: flex;
  height: 132px;
  position: relative;
  z-index: 2;
  align-items: center;
}

.nav-link__wrapper-line {
  content: '';
  display: flex;
  align-items: center;
  height: 89px;
  width: 1px;
  background-color: #adc7e1;
  position: absolute;
  right: 0;
}

.nav-link__wrapper:hover {
  background-color: #0f1e2d;
  z-index: 99999;

  a {
    color: #fff;
  }

  .nav-link__wrapper-line {
    background-color: #0f1e2d;
  }
}

.nav-link__item.active {
  background-color: #0f1e2d;
  color: #fff;
}

.fisrt-level-menu {
  border-bottom: 1px solid #adc7e1;
}

.first-level__nav-link {
  font-weight: normal;
  font-size: 18px;
  line-height: 150%;
  letter-spacing: 0.02em;
  color: #adc7e1;
  text-decoration: none;
}

.first-level__nav-link::before {
  content: "0" counter(item);
  counter-increment: item;
  display: block;
  font-size: 12px;
  line-height: 15px;
  margin-bottom: 10px;
}

.nav-link__wrapper_company-line {
  height: 60px;
}

.second-level__nav-link {
  font-weight: normal;
  font-size: 14px;
  text-decoration: none;
  line-height: 160%;
  letter-spacing: 0.02em;
  color: #adc7e1;
}

.second-level-menu {
  .navbar {
    .nav-link__wrapper {
      max-width: 385px;
    }
  }
}

.d-none {
  display: none;
}

.burger {
  display: none;
  flex-direction: column;
  cursor: pointer;
}

.burger span {
  height: 2px;
  width: 28px;
  background-color: #00488e;
  border-radius: 2px solid #00488e;
  margin-bottom: 5px;
}

.after-link__arrow {
  width: 7px;
  height: 7px;
  border: 1px solid;
  border-color: #fff #0f1e2d #0f1e2d #fff;
  transform: rotate(45deg);
  transition: border-color 0.5s ease-in-out, bottom 0.5s ease-in-out;
  transition: 0.2s;
  margin-left: 8px;
}

.after-link__arrow.active {
  border-color: #00488e #fff #fff #00488e;
}

@media (max-width: 1450px) {
  .navbar-menu__link {
    margin-right: 20px;
  }

  .header-line {
    margin-right: 20px;
  }

  .first-level__nav-link {
    font-size: 14px;
    line-height: 125%;
  }
}

@media (max-width: 1250px) {
  #header {
    padding-top: 24px;
    padding-bottom: 24px;
  }

  .burger {
    display: flex;
  }

  .navbar {
    display: none;
  }

  .contacts-btn__block {
    display: none;
  }

  .lang-btn_wrapper {
    display: none;
  }

  .mobile-menu__wrapper {
    background-color: #fff;
    width: 70%;
    height: 100vh;
    position: absolute;
    right: -70%;
    padding: 24px 16px;
    transition: 0.2s;
  }

  .mobile-menu__wrapper.active {
    right: 0;
  }

  .mobile-menu-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .mobile-menu-search {
    margin-right: 0;
    padding: 16px 0;
    height: auto;
  }

  .mobile-navbar {
    overflow: scroll;
  }

  .mobile-navbar__link {
    margin-right: 0;
    padding: 16px 0;
    height: auto;
    align-items: flex-start;
  }

  .mobile-submenu {
    display: flex;
    flex-direction: column;
    height: 0;
    overflow: hidden;
    transition: 0.2s;
  }

  .mobile-submenu__links {
    height: 0;
    overflow: hidden;
    transition: 0.2s;
    display: flex;
    flex-direction: column;
  }

  .mobile-submenu a {
    font-weight: normal;
    font-size: 13px;
    line-height: 14px;
    letter-spacing: 0.02em;
    color: #0f1e2d;
    text-decoration: none;
    margin-bottom: 12px;
  }

  .mobile-submenu__links a {
    margin-left: 10px;
    color: #00488e;
    margin-bottom: 6px;
  }

  .mobile-submenu_third {
    font-weight: normal;
    font-size: 13px;
    line-height: 14px;
    letter-spacing: 0.02em;
    color: #0f1e2d;
    text-decoration: none;
    margin-bottom: 12px;
  }

  .mobile-submenu__wrapper {
    display: flex;
  }

  .mobile-menu__footer-block {
    position: absolute;
    bottom: 24px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .mobile-menu-btn {
    width: 250px;
    background: #00488e;
    border-radius: 2px;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    letter-spacing: 0.02em;
    color: #fff;
    border: none;
    padding: 16px 0;
  }

  .mobile-lang-btn {
    display: flex;
    margin-top: 16px;
  }

  .mobile-menu-search input {
    width: 100%;
  }

  .nav-link {
    font-size: 17px;
  }

  .lang-btn {
    font-size: 14px;
  }

  .mobile-navbar__link.active a {
    color: #00488e;
  }

  .mobile-submenu__wrapper > .after-link__arrow {
    margin-top: 3px;
  }

  .mobile-submenu__item {
    margin-top: 4px;
  }
}

@media (max-width: 576px) {
  .nav-link {
    font-size: 13px;
  }

  .mobile-submenu a {
    font-size: 12px;
  }

  .after-link__arrow {
    height: 5px;
    width: 5px;
  }

  .mobile-navbar__link {
    align-items: center;
  }

  .mobile-menu__footer-block {
    width: initial;
  }
}

@media (max-width: 410px) {
  .mobile-menu__footer-block {
    width: 90%;
  }

  .mobile-menu-btn {
    width: 190px;
  }
}

@media (max-height: 415px) {
  .mobile-menu__wrapper {
    overflow-y: auto;
  }

  .mobile-menu__footer-block {
    position: static;
  }
}
