#more-contacts {
  padding-top: 40px;
}

.more-contacts {
  display: flex;
  justify-content: space-between;
}

.more-contacts__title {
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #00488e;
}

.more-contacts__text {
  font-weight: normal;
  font-size: 16px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
  margin-top: 16px;
}

.more-contacts__text a {
  text-decoration: none;
  color: #0f1e2d;
}

.more-contacts__text.mail a {
  color: #00488e;
}

@media (max-width: 1050px) {
  .more-contacts {
    flex-wrap: wrap;
  }

  .more-contacts__item {
    margin-right: 20px;
    margin-bottom: 20px;
  }
}
