.tab-block-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  counter-reset: item;
}

.tab-block-link {
  color: #9a9da0;
  font-weight: normal;
  font-size: 18px;
  line-height: 150%;
  letter-spacing: 0.02em;
  cursor: pointer;
  text-decoration: none;
}

.tab-block-link.active {
  color: #00488e;
}

.tab-block-link::before {
  content: '0' counter(item);
  counter-increment: item;
  display: flex;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  color: inherit;
  margin-bottom: 10px;
}

.tab-separation-line {
  height: 84px;
  width: 1px;
  background: #9a9da0;
  display: flex;
}

.nikko-tab {
  margin-top: 23px;
}

.nikko-tab__wrapper {
  display: flex;
  align-items: center;
  width: 100%;
}

.nikko-tab__item {
  height: 598px;
}

.nikko-tab__item:first-child {
  background: #00488e;
  flex-basis: 38%;
  position: relative;
}

.nikko-tab__item:last-child img {
  height: 598px;
  object-fit: cover;
}

.nikko-tab__item-text {
  font-weight: normal;
  font-size: 18px;
  line-height: 200%;
  letter-spacing: 0.02em;
  padding: 40px 70px 40px 40px;
  color: #fff;
}

.nikko-tab__item-link_block {
  position: absolute;
  bottom: 40px;
  left: 40px;
}

.nikko-tab__item-link_block a {
  text-decoration: none;
  color: #fff;
  font-weight: normal;
  font-size: 18px;
  line-height: 150%;
  letter-spacing: 0.02em;
  display: flex;
  align-items: center;
}

.nikko-tab__item-link_block a::after {
  content: '';
  display: flex;
  width: 48px;
  height: 48px;
  border: 1px solid #e6edf3;
  border-radius: 50%;
  background: url(../images/arrowWhite.svg) no-repeat center 19px;
  margin-left: 24px;
}

@media (max-width: 1150px) {
  .nikko-tab__item-link_block a::after {
    margin-left: 10px;
  }

  .nikko-tab__item:first-child {
    flex-basis: 50%;
  }

  .nikko-tab__item-text {
    line-height: 145%;
  }
}

@media (max-width: 992px) {
  .tab-block-link {
    font-size: 13px;
  }

  .nikko-tab__item:first-child {
    flex-basis: 80%;
  }

  .nikko-tab__item:last-child img {
    object-fit: fill;
  }
}

@media (max-width: 768px) {
  .tab-block-header {
    flex-wrap: wrap;
  }

  .tab-block-link {
    text-align: center;
    margin-bottom: 30px;
  }

  .tab-separation-line {
    display: none;
  }

  .tab-block-link::before {
    justify-content: center;
  }

  .nikko-tab__wrapper {
    flex-direction: column;
  }

  .nikko-tab__item:first-child {
    order: 2;
  }

  .nikko-tab__item-link_block {
    position: static;
    padding: 0 20px 20px 40px;
  }

  .nikko-tab__item:last-child img {
    object-fit: cover;
  }
}

@media (max-width: 576px) {
  .nikko-tab__item:last-child img {
    object-fit: contain;
    height: auto;
  }

  .nikko-tab__item {
    height: auto;
  }

  .tab-block-link {
    margin-right: 10px;
    font-size: 11px;
  }
}
