#company-contacts {
  padding-top: 140px;
}

.company-contacts {
  display: flex;
  justify-content: space-between;
  padding-bottom: 80px;
  border-bottom: 1px solid #e6edf3;
}

.bottom-img__text {
  font-style: italic;
  font-weight: normal;
  font-size: 14px;
  line-height: 160%;
  text-align: center;
  letter-spacing: 0.02em;
  color: #9a9da0;
  margin-top: 20px;
}

.company-contacts__title {
  font-weight: 600;
  font-size: 28px;
  line-height: 140%;
  color: #00488e;
  width: 520px;
  margin-bottom: 40px;
}

.company-contacts__list-item {
  font-weight: normal;
  font-size: 20px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
  margin-bottom: 16px;
}

.company-contacts__link {
  font-weight: normal;
  font-size: 20px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #00488e;
  margin-bottom: 16px;
}

.company-contacts__link-item {
  font-weight: normal;
  font-size: 20px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #0f1e2d;
  margin-bottom: 16px;
  text-decoration: none;
}

.company-contacts__item:first-child {
  flex-basis: 54%;
}

.company-contacts__list-item span {
  font-weight: 600;
}

@media (max-width: 1550px) {
  .company-contacts__item:first-child {
    margin-right: 20px;
  }

  .company-contacts__title {
    font-size: 20px;
    width: 326px;
  }

  .company-contacts__list-item {
    font-size: 15px;
  }
}

@media (max-width: 1200px) {
  #company-contacts {
    padding-top: 20px;
  }

  .company-contacts {
    flex-direction: column;
    padding-bottom: 10px;
  }

  .company-contacts__item {
    margin-top: 30px;
  }

  .company-contacts__item:first-child {
    margin-right: 0;
  }

  .company-contacts__item img {
    width: 100%;
  }
}
