#search-block {
  padding-top: 140px;
}

.search-block {
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.faild-search {
  font-weight: 600;
  font-size: 20px;
  line-height: 200%;
  text-align: center;
  letter-spacing: 0.02em;
  color: #9a9da0;
  margin-bottom: 40px;
}

.scs-search {
  font-weight: 600;
  font-size: 20px;
  line-height: 200%;
  text-align: center;
  letter-spacing: 0.02em;
  color: #00488e;
}

.success-search__message-item:not(:first-child) {
  padding: 40px 0;
  border-bottom: 1px solid #e6edf3;
}

.success-search__title {
  font-weight: 600;
  font-size: 24px;
  line-height: 140%;
  color: #0f1e2d;
}

.success-search__path {
  font-size: 20px;
  line-height: 200%;
  letter-spacing: 0.02em;
  color: #9a9da0;
}

.success-search__path span {
  font-weight: 600;
  color: #0f1e2d;
}
